body{
    font-family: 'Montserrat', sans-serif;
}


/************************************* Navigation bar ****************************************/
.hamburger{
    color: white;
    size: 2rem;
}

script{
    color: white;
}

nav{
    position: absolute !important;
    top: 0;
    z-index: 2;
}

.container-fluid{
    width: 100vw;
}

#navbarSupportedContent{
    text-align: center;
}

.nav-link{
    font-size: .8rem;
    letter-spacing: 0.0375rem;
    font-family: 'Montserrat', sans-serif;
    cursor:pointer;
    color: white !important;
    margin-left: 0.75rem;
    margin-right: 0.75rem;
}

.nav-link::after{
    content:'';
    display: block;
    width: 0;
    height: 2px;
    background: white;
    transition: width .3s;
}

.nav-link:hover::after{
    width: 100%;
    display: block;
    transition: width .3s;
}

/************************************* Header ****************************************/

/* .header-wrapper{
    background-image:url(./bulb.jpg) no-repeat;
    position: relative;
    background-size: cover;
    background-position: center;
    height: 90vh;
} */



.header-wrapper{
    background-color: #000000;
    height: 100vh;
}


.main-info{
    display: flex;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.main-info h1{
    color: rgb(96, 92, 92);
    font-family: 'Euphoria Script', cursive;
    font-size: 6.75rem;
}

.main-info h1:hover{
    animation-name: colour;
    animation-duration: 2s;
    transition-timing-function: ease-in-out;
    animation-iteration-count: infinite;
}

@keyframes colour{
    0% {color: rgb(201, 10, 10)}
    10% {color: rgb(201, 125, 10)}
    20% {color: rgb(201, 185, 10)}
    30% {color: rgb(172, 227, 9)}
    40% {color: rgb(76, 247, 3)}
    50% {color: rgb(2, 217, 255)}
    55% {color: rgb(2, 145, 255)}
    60% {color: rgb(10, 86, 201)}
    70% {color: rgb(110, 46, 237)}
    80% {color: rgb(193, 15, 242)}
    90% {color: rgb(185, 10, 201)}
    100% {color: rgb(201, 10, 147)}
    0% {color: rgb(255, 0, 60)}
}

/************* TYpes Text */

.typetext{
    display: inline-flex !important;

}
.typed-text{
    font-size: 2rem;
    color:chartreuse;
    font-family: 'Courier Prime', monospace;
}

@media(max-width:768px){
    .main-info h1{
        font-size: 1.7rem !important;
    } 
}

@media(max-width:768px){
    .typed-text{
        font-size: 1.3rem !important;
    } 
}

/************* Particles */

.tsparticles-canvas-el{
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}

@media(max-width:768px){
    .particle-canvas{
        display: none;
    }
}

/************************************* About ****************************************/

.about-heading{
    font-family: 'Rancho', cursive;
