}

.vertical-timeline{
    margin: 0 0;
}

.vertical-timeline-element-icon{
    border-radius: 0 !important;
    box-shadow: none !important;
    -webkit-box-shadow:none !important; 
}

.vertical-timeline-element-content{
    border-radius: 1rem !important;
    width: 50rem;

}

.vertical-timeline::before{
